ul,
ol,
li {
  margin: 0;
  padding: 0; }

body {
  color: #363636;
  font-size: 18px;
  font-weight: 400; }

a {
  color: #2179cd;
  text-decoration: none;
  cursor: pointer; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400; }

h1 {
  font-size: 24px;
  line-height: 32px; }

h2 {
  font-size: 20px;
  line-height: 28px; }

h3 {
  font-size: 16px;
  line-height: 24px; }

h4 {
  font-size: 13px;
  line-height: 24px; }

h5 {
  font-size: 12px;
  line-height: 20px; }

h6 {
  font-size: 10px;
  line-height: 20px; }

.register-box {
  width: 444px;
  margin: calc(50vh - 274px) auto 0;
  padding: 10px;
  border: solid 1px #84c688;
  border-radius: 20px;
  background-color: #fff;
  -webkit-box-shadow: 0 14px 14px 0 rgba(0, 0, 0, 0.25), 0 1px 6px rgba(0, 0, 0, 0.12);
          box-shadow: 0 14px 14px 0 rgba(0, 0, 0, 0.25), 0 1px 6px rgba(0, 0, 0, 0.12); }

.register-box-header-left {
  margin-bottom: 10px;
  margin-left: 200px;
  color: white;
  font-size: 20px; }

:invalid {
  color: #db4437; }

.register-login-span {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 200px;
  color: #50ae55;
  font-family: 'Verdana', sans-serif;
  font-size: 14px;
  text-align: left;
  text-decoration: none; }

.register-login-link {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 15px;
  color: white;
  font-size: 18px;
  text-align: left;
  text-decoration: none; }

.register-message {
  display: block;
  width: 294px;
  margin: 10px auto;
  padding: 15px;
  border-radius: 5px;
  background-color: #fff;
  color: #50ae55;
  text-align: center;
  overflow: hidden; }

.err-msg-p {
  color: #db4437;
  text-align: center; }

.success-msg-p {
  color: #50ae55;
  font-size: 14px;
  text-align: center; }

.reg-form-text {
  margin-left: 60px; }



