ul,
ol,
li {
  margin: 0;
  padding: 0; }

body {
  color: #363636;
  font-size: 18px;
  font-weight: 400; }

a {
  color: #2179cd;
  text-decoration: none;
  cursor: pointer; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400; }

h1 {
  font-size: 24px;
  line-height: 32px; }

h2 {
  font-size: 20px;
  line-height: 28px; }

h3 {
  font-size: 16px;
  line-height: 24px; }

h4 {
  font-size: 13px;
  line-height: 24px; }

h5 {
  font-size: 12px;
  line-height: 20px; }

h6 {
  font-size: 10px;
  line-height: 20px; }

.wishlist-list {
  margin: 10px;
  border: solid 1px #84c688;
  border-radius: 5px;
  background-color: #edf7ed; }

.wishlist-list-titles {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  min-height: 48px;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
  background-color: #50ae55; }

.wishlist-list-element {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.6);
  color: #50ae55; }

.element-name {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 400px; }

.element-name-img {
  display: inline-block;
  width: 100px; }

.element-name-link {
  margin-left: 10px;
  color: #387a3b;
  text-decoration: none; }

.element-name-link:hover {
  text-shadow: 3px 3px 8px #387a3b; }

.element-city {
  width: 150px;
  text-align: center; }

.element-date {
  width: 100px;
  text-align: center; }

.element-sum {
  width: 80px;
  text-align: center; }

.element-title {
  color: #fff;
  font-size: 12px;
  text-align: center; }

.element-title-name {
  margin-left: 80px; }

.btn-item-remove {
  position: relative;
  width: 15px;
  height: 15px;
  margin-right: 10px;
  border: none;
  outline: none;
  color: transparent;
  content: ' ';
  opacity: .4; }

.btn-item-remove:hover {
  outline: none;
  color: transparent;
  opacity: 1; }

.btn-item-remove::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  background: url("/img/icons/remove.svg") 0 0 no-repeat;
  background-size: contain;
  content: ' ';
  cursor: pointer; }



