ul,
ol,
li {
  margin: 0;
  padding: 0; }

body {
  color: #363636;
  font-size: 18px;
  font-weight: 400; }

a {
  color: #2179cd;
  text-decoration: none;
  cursor: pointer; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400; }

h1 {
  font-size: 24px;
  line-height: 32px; }

h2 {
  font-size: 20px;
  line-height: 28px; }

h3 {
  font-size: 16px;
  line-height: 24px; }

h4 {
  font-size: 13px;
  line-height: 24px; }

h5 {
  font-size: 12px;
  line-height: 20px; }

h6 {
  font-size: 10px;
  line-height: 20px; }

.preloader {
  display: inline-block;
  position: absolute;
  top: calc(50% - 150px);
  left: calc(50% - 150px);
  width: 300px;
  height: 300px;
  border: transparent;
  border-radius: 50px;
  background-color: #50ae55;
  letter-spacing: 5px;
  text-align: center;
  -webkit-box-shadow: 0 0 30px 1px #fff inset;
          box-shadow: 0 0 30px 1px #fff inset; }

.preloader-1 {
  width: 66px;
  height: 12px;
  margin: 130px auto 0;
  letter-spacing: 5px; }

.preloader-min {
  display: inline-block;
  position: absolute;
  top: calc(50% - 40px);
  left: calc(50% - 150px);
  width: 300px;
  height: 80px;
  border: transparent;
  border-radius: 20px;
  background-color: #50ae55;
  letter-spacing: 5px;
  text-align: center;
  -webkit-box-shadow: 0 0 30px 1px #fff inset;
          box-shadow: 0 0 30px 1px #fff inset; }

.preloader-min-1 {
  width: 66px;
  height: 12px;
  margin: 20px auto 0;
  letter-spacing: 5px; }

.load {
  margin: 5px 0;
  color: #fff;
  font-family: 'Arial', sans-serif;
  font-size: 10px;
  letter-spacing: 3px;
  text-align: center;
  text-transform: uppercase; }

.preloader-1 .line {
  display: inline-block;
  width: 1px;
  height: 20px;
  margin: 0 3px;
  background: #fff;
  letter-spacing: 5px;
  -webkit-animation: opacity-1 1000ms infinite ease-in-out;
          animation: opacity-1 1000ms infinite ease-in-out; }

.preloader-min-1 .line {
  display: inline-block;
  width: 1px;
  height: 20px;
  margin: 0 3px;
  background: #fff;
  letter-spacing: 5px;
  -webkit-animation: opacity-1 1000ms infinite ease-in-out;
          animation: opacity-1 1000ms infinite ease-in-out; }

.preloader-1 .line-1 {
  -webkit-animation-delay: 800ms;
          animation-delay: 800ms; }

.preloader-1 .line-2 {
  -webkit-animation-delay: 600ms;
          animation-delay: 600ms; }

.preloader-1 .line-3 {
  -webkit-animation-delay: 400ms;
          animation-delay: 400ms; }

.preloader-1 .line-4 {
  -webkit-animation-delay: 200ms;
          animation-delay: 200ms; }

.preloader-1 .line-6 {
  -webkit-animation-delay: 200ms;
          animation-delay: 200ms; }

.preloader-1 .line-7 {
  -webkit-animation-delay: 400ms;
          animation-delay: 400ms; }

.preloader-1 .line-8 {
  -webkit-animation-delay: 600ms;
          animation-delay: 600ms; }

.preloader-1 .line-9 {
  -webkit-animation-delay: 800ms;
          animation-delay: 800ms; }

.preloader-min-1 .line-1 {
  -webkit-animation-delay: 800ms;
          animation-delay: 800ms; }

.preloader-min-1 .line-2 {
  -webkit-animation-delay: 600ms;
          animation-delay: 600ms; }

.preloader-min-1 .line-3 {
  -webkit-animation-delay: 400ms;
          animation-delay: 400ms; }

.preloader-min-1 .line-4 {
  -webkit-animation-delay: 200ms;
          animation-delay: 200ms; }

.preloader-min-1 .line-6 {
  -webkit-animation-delay: 200ms;
          animation-delay: 200ms; }

.preloader-min-1 .line-7 {
  -webkit-animation-delay: 400ms;
          animation-delay: 400ms; }

.preloader-min-1 .line-8 {
  -webkit-animation-delay: 600ms;
          animation-delay: 600ms; }

.preloader-min-1 .line-9 {
  -webkit-animation-delay: 800ms;
          animation-delay: 800ms; }

@-webkit-keyframes opacity-1 {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes opacity-1 {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes opacity-2 {
  0% {
    height: 15px;
    opacity: 1; }
  50% {
    height: 12px;
    opacity: 0; }
  100% {
    height: 15px;
    opacity: 1; } }

@keyframes opacity-2 {
  0% {
    height: 15px;
    opacity: 1; }
  50% {
    height: 12px;
    opacity: 0; }
  100% {
    height: 15px;
    opacity: 1; } }



