ul,
ol,
li {
  margin: 0;
  padding: 0; }

body {
  color: #363636;
  font-size: 18px;
  font-weight: 400; }

a {
  color: #2179cd;
  text-decoration: none;
  cursor: pointer; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400; }

h1 {
  font-size: 24px;
  line-height: 32px; }

h2 {
  font-size: 20px;
  line-height: 28px; }

h3 {
  font-size: 16px;
