.event-item {
  display: inline-block;
  position: relative;
  width: 290px;
  margin: 4px;
  padding-bottom: 6px;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  border: 1px solid #84c688;
  border-radius: 6px; }

.event-item:hover {
  -webkit-box-shadow: 3px 5px 7px 0 rgba(0, 0, 0, 0.2), 0 1px 6px rgba(0, 0, 0, 0.12);
          box-shadow: 3px 5px 7px 0 rgba(0, 0, 0, 0.2), 0 1px 6px rgba(0, 0, 0, 0.12); }

.event-item-img-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  height: 230px;
  margin-bottom: 5px;
  border-radius: 4px 4px 0 0;
  background-color: black;
  overflow: hidden; }

.event-item-img {
  height: 100%; }

.event-item-name {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  position: absolute;
  top: 14px;
  margin-right: 15px;
  padding: 5px 5px 5px 10px;
  background-color: rgba(0, 0, 0, 0.4);
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  text-transform: capitalize; }

.event-item-price {
  position: absolute;
  right: 0;
  bottom: 15px;
  padding: 6px 6px 6px 15px;
  color: #387a3b;
  font-size: 20px;
  font-weight: 700; }

.event-item-date {
  margin: 8px;
  color: #387a3b;
  font-size: 12px;
  text-transform: uppercase; }

.event-item-city {
  margin: 8px;
  color: #387a3b;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase; }

.icon {
  width: 25px;
  height: 25px;
  margin-left: 16px; }

.star-icon-event {
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 50%;
  background: rgba(255, 200, 0, 0.8); }

.empty-star-icon-event {
