ul,
ol,
li {
  margin: 0;
  padding: 0; }

body {
  color: #363636;
  font-size: 18px;
  font-weight: 400; }

a {
  color: #2179cd;
  text-decoration: none;
  cursor: pointer; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400; }

h1 {
  font-size: 24px;
  line-height: 32px; }

h2 {
  font-size: 20px;
  line-height: 28px; }

h3 {
  font-size: 16px;
  line-height: 24px; }

h4 {
  font-size: 13px;
  line-height: 24px; }

h5 {
  font-size: 12px;
  line-height: 20px; }

h6 {
  font-size: 10px;
  line-height: 20px; }

body {
  font-family: 'Roboto', sans-serif; }

.btn {
  display: inline-block;
  height: 30px;
  margin-bottom: 10px;
  border: solid 1px #50ae55;
  background-color: #50ae55;
  color: #fff; }

.btn:hover {
  background-color: #50ae55;
  color: #fff;
  -webkit-box-shadow: 3px 5px 7px 0 rgba(0, 0, 0, 0.2), 0 1px 6px rgba(0, 0, 0, 0.12);
          box-shadow: 3px 5px 7px 0 rgba(0, 0, 0, 0.2), 0 1px 6px rgba(0, 0, 0, 0.12); }

.btn:hover:disabled {
  border-color: #edf7ed;
  background-color: #edf7ed; }

.btn:active {
  background-color: #387a3b;
  color: #fff;
  -webkit-box-shadow: 2px 3px 5px 0 rgba(0, 0, 0, 0.4), 0 1px 6px rgba(0, 0, 0, 0.12);
          box-shadow: 2px 3px 5px 0 rgba(0, 0, 0, 0.4), 0 1px 6px rgba(0, 0, 0, 0.12); }

.btn:disabled {
  border-color: #edf7ed;
  color: #edf7ed; }



