  font-weight: 400;
  line-height: 21px;
  text-align: left; }

.fileContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  margin: 20px;
  color: #387a3b;
  font-size: 12px;
  cursor: pointer;
  overflow: hidden; }

.fileContainer [type=file] {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  min-width: 100%;
  min-height: 100%;
  font-size: 15px;
  text-align: right;
  cursor: inherit;
  opacity: 0;
  filter: alpha(opacity=0); }

.user-icon {
  width: 25px;
  height: 25px;
  margin-right: 10px; }

.btn-upload {
  position: relative;
  width: 100px;
  height: 35px;
  margin: 0 20px;
  padding-left: 20px;
  border-radius: 10px;
  outline: none;
  content: ' ';
  cursor: pointer; }

.btn-upload::before {
  position: absolute;
  top: 6px;
  left: 8px;
  width: 20px;
  height: 20px;
  background: url("/img/icons/upload.svg") 0 0 no-repeat;
  content: ' ';
  cursor: pointer; }



