.btn-down {
  position: relative;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  outline: none;
  background-color: transparent;
  content: ' ';
  cursor: pointer;
  opacity: .8; }

.btn-down:active {
  outline: none;
  opacity: 1; }

.btn-down::before {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 20px;
  height: 20px;
  background: url("/img/icons/down.svg") 0 0 no-repeat;
  content: ' ';
  cursor: pointer; }

.btn-up {
