ul,
ol,
li {
  margin: 0;
  padding: 0; }

body {
  color: #363636;
  font-size: 18px;
  font-weight: 400; }

a {
  color: #2179cd;
  text-decoration: none;
  cursor: pointer; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400; }

h1 {
  font-size: 24px;
  line-height: 32px; }

h2 {
  font-size: 20px;
  line-height: 28px; }

h3 {
  font-size: 16px;
  line-height: 24px; }

h4 {
  font-size: 13px;
  line-height: 24px; }

h5 {
  font-size: 12px;
  line-height: 20px; }

h6 {
  font-size: 10px;
  line-height: 20px; }

.login-box {
  width: 444px;
  margin: calc(50vh - 274px) auto 0;
  padding: 10px;
  border: solid 1px #84c688;
  border-radius: 20px;
  background-color: #fff;
  -webkit-box-shadow: 0 14px 14px 0 rgba(0, 0, 0, 0.25), 0 1px 6px rgba(0, 0, 0, 0.12);
          box-shadow: 0 14px 14px 0 rgba(0, 0, 0, 0.25), 0 1px 6px rgba(0, 0, 0, 0.12); }

.login-box-header-center {
  margin-bottom: 10px;
  color: #50ae55;
  font-size: 20px;
  text-align: center; }

.warning-text {
  font-size: 18px; }

.login-input-box {
  display: block;
  width: 284px;
  height: 46px;
  margin: 7px auto;
  padding-right: 19px;
  padding-left: 19px;
  border: 1px solid #84c688;
  border-radius: 3px;
  background-color: #edf7ed;
  color: #387a3b;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  text-align: left; }

.login-input-box::-webkit-input-placeholder {
  color: #84c688; }

.login-input-box:-ms-input-placeholder {
  color: #84c688; }

.login-input-box::-ms-input-placeholder {
  color: #84c688; }

.login-input-box::placeholder {
  color: #84c688; }

.input-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 322px;
  margin-top: 15px;
  margin-bottom: 20px;
  margin-left: 62px; }

.login-help-block {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 15px;
  padding: 20px;
  -webkit-transition: opacity .6s;
  -o-transition: opacity .6s;
  transition: opacity .6s;
  background-color: #f44336;
  color: white;
  opacity: 1; }

.warn-close {
  margin-left: 15px;
  float: right;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  color: white;
  font-size: 22px;
  font-weight: bold;
  line-height: 20px;
  cursor: pointer; }

.warn-close:hover {
  color: black; }

.login-btn {
  display: block;
  width: 322px;
  height: 46px;
  margin: 10px auto;
  border-radius: 5px;
  font-size: 18px;
  font-weight: 700;
  text-align: center; }

.login-btn:focus {
  outline: none; }

.login-reg-link {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 5px;
  border-bottom: solid 1px transparent;
  color: #db4437;
  font-size: 18px;
  text-align: left;
  text-decoration: none; }

.login-msg {
  text-align: center; }

.error-msg {
  padding: 5px;
  color: #db4437;
  font-size: 14px;
  font-weight: 300; }

.success-msg {
  padding: 30px;
  color: #50ae55;
  font-size: 14px;
  font-weight: 700; }

.logout-msg {
  color: #50ae55;
  font-size: 20px;
  text-align: center; }

.button-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 20px; }

.logout-btn {
  width: 100px;
  height: 46px;
  margin-top: 9px;
  margin-bottom: 10px;
  border-radius: 5px;
  font-size: 18px;
  text-align: center; }

.login-input-label {
  margin-left: 7px;
  color: #50ae55;
  font-size: 14px; }

.forgot-pass-link {
  border-bottom: solid 1px transparent;
  color: #db4437;
  text-decoration: none; }

.forgot-pass-link:hover,
.login-reg-link:hover {
  text-shadow: 3px 3px 4px grey; }

.forgot-msg {
  display: block;
  width: 320px;
  margin-bottom: 5px;
  margin-left: 65px; }

.login-form-text {
  margin-left: 60px; }



