ul,
ol,
li {
  margin: 0;
  padding: 0; }

body {
  color: #363636;
  font-size: 18px;
  font-weight: 400; }

a {
  color: #2179cd;
  text-decoration: none;
  cursor: pointer; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400; }

h1 {
  font-size: 24px;
  line-height: 32px; }

h2 {
  font-size: 20px;
  line-height: 28px; }

h3 {
  font-size: 16px;
  line-height: 24px; }

h4 {
  font-size: 13px;
  line-height: 24px; }

h5 {
  font-size: 12px;
  line-height: 20px; }

h6 {
  font-size: 10px;
  line-height: 20px; }

.message-container {
  display: inline-block;
  position: absolute;
  top: calc(0% + 350px);
  left: calc(50% - 150px);
  width: 300px;
  height: 200px;
  -webkit-transition: all 2s;
  -o-transition: all 2s;
  transition: all 2s;
  border: 3px solid #50ae55;
  border-radius: 10px;
  background-color: #fff;
  -webkit-box-shadow: 0 4px 18px 0 rgba(0, 0, 0, 0.52);
          box-shadow: 0 4px 18px 0 rgba(0, 0, 0, 0.52); }

.message-p {
  margin-top: 75px;
  padding: 15px;
  color: #387a3b;
  font-size: 20px;
  text-align: center; }



