ul,
ol,
li {
  margin: 0;
  padding: 0; }

body {
  color: #363636;
  font-size: 18px;
  font-weight: 400; }

a {
  color: #2179cd;
  text-decoration: none;
  cursor: pointer; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400; }

h1 {
  font-size: 24px;
  line-height: 32px; }

h2 {
  font-size: 20px;
  line-height: 28px; }

h3 {
  font-size: 16px;
  line-height: 24px; }

h4 {
  font-size: 13px;
  line-height: 24px; }

h5 {
  font-size: 12px;
  line-height: 20px; }

h6 {
  font-size: 10px;
  line-height: 20px; }

.about {
  min-height: calc(100vh - 178px); }

.about-page-root {
  width: 444px;
  margin-top: calc(50vh - 200px);
  margin-right: auto;
  margin-left: auto;
  padding: 30px 20px;
  border: solid 1px #84c688;
  border-radius: 20px;
  color: #50ae55;
  -webkit-box-shadow: 0 14px 14px 0 rgba(0, 0, 0, 0.25), 0 1px 6px rgba(0, 0, 0, 0.12);
          box-shadow: 0 14px 14px 0 rgba(0, 0, 0, 0.25), 0 1px 6px rgba(0, 0, 0, 0.12); }

.about-head {
  color: #387a3b;
  font-size: 20px; }

.about-info {
  margin-top: 5px;
  margin-bottom: 15px;
  font-size: 16px; }

.about-goal {
  color: #387a3b;
  font-size: 20px; }

.about-content {
  margin: 5px 0;
  font-size: 16px; }



