ul,
ol,
li {
  margin: 0;
  padding: 0; }

body {
  color: #363636;
  font-size: 18px;
  font-weight: 400; }

a {
  color: #2179cd;
  text-decoration: none;
  cursor: pointer; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400; }

h1 {
  font-size: 24px;
  line-height: 32px; }

h2 {
  font-size: 20px;
  line-height: 28px; }

h3 {
  font-size: 16px;
  line-height: 24px; }

h4 {
  font-size: 13px;
  line-height: 24px; }

h5 {
  font-size: 12px;
  line-height: 20px; }

h6 {
  font-size: 10px;
  line-height: 20px; }

.footer-container {
  display: block;
  background-color: #db4437; }

.footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  max-width: 1200px;
  margin: 10px auto;
  padding-top: 20px; }

.copyright {
  display: inline-block;
  padding: 0 15px; }

.copyright-p {
  color: #fff;
  font-size: 12px; }

.footer-link {
  margin: 0 30px 20px;
  border-bottom: solid 1px transparent;
  color: #fff;
  font-size: 16px;
  text-decoration: none; }

.footer-link:hover {
  border-bottom: solid 1px #fff; }



